import React, { forwardRef, useImperativeHandle } from "react";
import { CountdownCircleTimer } from "react-countdown-circle-timer";
import "./styles.css";

export function TimerComponent(props) {
  // console.log(props.durationTime)
  const renderTime = ({ remainingTime }) => {
    // if(props.durationTime-remainingTime && !props.isPlaying ){
    //   console.log('render time')
    //   props.remainingTimeHandler(props.durationTime-remainingTime)
    // }
    let lastTime = 0;
    var timerhours = Math.floor(remainingTime / 3600);
    var timerminutes = -timerhours * 60 + Math.floor(remainingTime / 60);
    var timerseconds = -timerminutes * 60 - timerhours * 3600 + remainingTime;
    if (remainingTime === 0) {
      timerminutes = 0;
      timerseconds = 0;
    }
    if(lastTime != remainingTime && props.onTick){
      lastTime=remainingTime; 
      props.onTick(`${timerminutes}:${timerseconds}`,lastTime)
     
    }
    return (
      <div className="timer">
        <div className="minutesvalue">
          {timerminutes}:{timerseconds}
        </div>
      </div>
    );
  };
  return (
    <div className="App">
      <div className={`${props.className} timer-wrapper`}>
        {!props.disableBtn && (
          <CountdownCircleTimer
            key={props.disableBtn}
            onComplete={(totalElapsedTime) => {
              if (totalElapsedTime === props.durationTime) {
                props.handleTimer();
              }
            }}
            isPlaying={props.isPlaying}
            size={window.screen.width <= 1024 ? 60 : 110}
            duration={props.durationTime}
            colors={[["#04724d", 0.1], ["#04724d", 0.33], ["#04724d"]]}
            strokeWidth={window.screen.width <= 1024 ? 6 : 8}
            // onTick={handleTick}
            precision={1000}
          >
            {renderTime}
          </CountdownCircleTimer>
        )}{" "}
        {props.disableBtn && (
          <CountdownCircleTimer
            onComplete={(totalElapsedTime) => {
              if (totalElapsedTime === props.durationTime) {
                props.handleTimer();
              }
            }}
            isPlaying={props.isPlaying}
            size={window.screen.width <= 1024 ? 60 : 110}
            duration={props.durationTime}
            colors={[["#04724d", 0.1], ["#04724d", 0.33], ["#04724d"]]}
            strokeWidth={window.screen.width <= 1024 ? 6 : 8}
          >
            {renderTime}
          </CountdownCircleTimer>
        )}
        {!!props.disableBtn && !props.disableBtn && (
          <CountdownCircleTimer
            isPlaying={props.isPlaying}
            size={120}
            duration={0}
            colors={[["#04724d", 0.1], ["#04724d", 0.33], ["#04724d"]]}
          >
            {renderTime}
          </CountdownCircleTimer>
        )}
      </div>
    </div>
  );
}
